<template>
    <h1>Twitter</h1>
    <input type="text" placeholder="Message:" v-model="mes" required/>
    <br />
    <br />
    <br />
    <br />
    <button v-on:click="getTwitter">Confirm</button>
</template>

<script>    
const qs = require('qs');

export default {
    name: "TwitterREA",
    data() {
        return {
            mes: null
        }
    },
    methods: {
        getTwitter(e) {
            e.preventDefault();
            if (this.mes != null) {
                    let url = 'http://localhost:8080/ar-user/'
                    let message = {
                        "message" : this.mes
                    }
                    let res = {
                        "action": {
                            "id" : localStorage.actionId,
                            "content" : localStorage.action
                        },
                        "reaction": [ 
                            {
                                "id" : "6366dc1caf9ba318ee9d8118",
                                "content" : JSON.stringify(message)
                            }
                        ]
                    }
                    console.log(res)
                    fetch(url ,{
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded',
                            'token' : localStorage.token,
                            "Accept" : "text/html",
                        },
                        body : qs.stringify(res),
                        mode : 'cors'
                    }
                    )
                    .then((res) => {
                        if (res.status === 201) {
                        res.text().then((ans) => {
                            console.log(ans)
                            this.$router.push({ name: "Home" });
                        })
                        } else {
                            console.log(res)
                            alert("Something Went Wrong");
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            } else {
                alert('Email required')
            }
        },
    }
}
</script>

